import Vue from 'vue'
import Router from 'vue-router'
//import HelloWorld from '@/views/HelloWorld'
import NoticeList from '@/views/notice/NoticeList'
import NoticeRegister from '@/views/notice/NoticeRegister'
import NoticeDetail from '@/views/notice/NoticeDetail'
import Layout from '@/components/Layout'
import Cookies from 'js-cookie'
import {setAuthHeader} from '@/api/app'

Vue.use(Router)

const requireAuth = (to, from, next) => {
  setAuthHeader(Cookies.get('token'))
  .then(res=>{
    alert(res.data.data)
    next()
  })
  .catch(err =>{
    Cookies.remove('token')
    alert(err.response.data.data)
    next()
  })
}

export default new Router({
  routes: [
    {path:'/login', component:() => import('@/views/login')},
    {
      path:'/',
      component:Layout,
      name:'registration',
      children:[
        {
          path:'/registration',
          component: () => import('@/views/registration/Registration')
        }
      ]
    },
    {
      path:'/',
      component:Layout,
      redirect:'/helloworld',
      name:'HelloWorld',  
      children:[
        {
          path:'/helloworld',
          component: () => import('@/views/HelloWorld')
        }
      ]
    },
    {
      path:'/notice',
      component:Layout,
      children:[
        {
          path:'list',
          name:'NoticeList',
          component: () => import('@/views/notice/NoticeList.vue'),
          beforeEnter (to, from, next) {
            console.log('==== to ====')
            console.log(to)
            console.log('==== to ====')
      
      
            console.log('==== from ====')
            console.log(from)
            console.log('==== from ====')
            
            console.log(Cookies.get('token'))
      
            if(Cookies.get('token')){
              if(to.path === '/login'){
                next({path:'/'})
              }else {
                next()
              }
            }else {
              if(to.path !== '/login'){
                next({path:'/login'})
                //next({path:'/login?redirect=${to.path}'})
              }else {
                next()
              }
            }
           }
        }
      ]
    },
    {
      path:'/notice',
      component:Layout,
      children:[
        {
          path:'detail',
          name:'NoticeDetail',
          component: () => import('@/views/notice/NoticeDetail.vue')
        }
      ]
    },
    {
      path:'/notice',
      component:Layout,
      children:[
        {
          path:'register',
          name:'NoticeRegister',
          component: () => import('@/views/notice/NoticeRegister.vue')
        }
      ]
    },
    {
      path:'/popup',
      component:Layout,
      children: [
        {
          path:'register',
          name:'PopupRegister',
          component:() => import('@/views/popup/PopupRegister')
        }
      ]
    }
  ]
})



