<template>
  <section class="detail">
    {{form}}
    <el-form ref="form" label-width="150px">
      <el-form-item label="제목">
        <el-input v-model="form.subj"></el-input>
      </el-form-item>
      <el-form-item label="헤드라인 등록">
        <el-radio-group v-model="form.tp">
          <el-radio label="헤드라인"></el-radio>
          <el-radio label="일반"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="팝업개시 대상" prop="">
        <el-checkbox-group v-model="form.init">
          <el-checkbox label="PC Web"></el-checkbox>
          <el-checkbox label="Mobile Web"></el-checkbox>
          <el-checkbox label="APP"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="내용">
        <vue-editor id="editor" v-model="form.conts"></vue-editor>
      </el-form-item>
    </el-form>
    <div class="bottomBtns">
      <el-button type="primary" v-if="!noticeId" @click="onSubmit">등록</el-button>
      <el-button type="primary" v-if="noticeId" @click="onModify">수정</el-button>
      <el-button type="primary" @click="$router.push('/notice/list')">취소</el-button>
    </div>
  </section>
</template>
<script>
  import { VueEditor } from 'vue2-editor'
  import axios from 'axios'
  export default {
    components: {
      VueEditor
    },
    data(){
      return{
        form:{
          subj:'',
          tp:'',
          init:[],
          conts:''
        },
        noticeId:this.$route.query.noticeId
      }
    },
    created(){
      if(this.noticeId){
        axios.get(`http://localhost:3000/notice/${this.noticeId}`)
        .then(res => {
            console.log(res)
            this.form = res.data.form

            this.form.init = res.data.form.init.split(',')
        })
        .catch(err => {

        })
        .finally(_ => {

        })
      }
    },
    methods:{
      onSubmit(){
        console.log('click')
        axios({
          method:'POST',
          url:'http://localhost:3000/notice',
          data:{form:this.form}
        })
        .then(res => {
          console.log('성공')
          this.$router.push({path:'/notice/detail', query:{noticeId:res.data.noticeId}})
        })
        .catch(err => {
          //
        })
        .finally(_ => {
          
        })
      },
      onModify(){
        axios.put(`http://localhost:3000/notice/${this.noticeId}`, {
          form:this.form
        })
        .then(res => {
          console.log('====res====')
          console.log(res)
          this.$router.push({path:'/notice/detail', query:{noticeId:res.data.noticeId}})
        })
        .catch(err => {
            console.log('실패')
        })
        .finally(_ => {
          console.log("무조건 실행... 수정 완료")
        })

      }
    }
  }
</script>
<style>

</style>



