<template>
  <section>
    <el-table :data="noticeData">
      <el-table-column prop="id" label="No" width="70" align="center"></el-table-column>
      <el-table-column prop="subj" label="제목" header-align="center">
        <template slot-scope="scope"><span class="link" @click="onDetail(scope)">{{scope.row.subj}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="tp" align="center" label="구분" width="150">
      </el-table-column>
      <el-table-column prop="createdAt" align="center" label="등록일" width="220">
      </el-table-column>
    </el-table>
    <el-pagination layout="prev, pager, next" 
      :page-size="pageSize" 
      :total="total"
      :current-page.sync="currentPage" 
      @current-change="onPageChange"> 
    </el-pagination>
    <div class="bottomBtns">
      <el-button type="primary" @click="$router.push('/notice/register')">공지사항 등록</el-button>
    </div>
  </section>
</template>

<script>
import axios from 'axios'
import camelCase from 'camelcase-keys'
import Cookies from 'js-cookie'

  export default {
   data(){
     return {
        noticeData:[],
        total:19,
        pageSize:10,
        currentPage:1
     }
   },
   created(){
     this.onList()
   },
   methods:{
     onDetail(scope){
       console.log(scope)
       this.$router.push({path:'/notice/detail', query:{noticeId:scope.row.id}})
     },
     onPageChange(){
        this.onList()
     },
     
     onList() {
       axios(`http://localhost:3000/notice`)
      .then(res => {
          console.log('====res ====')
          //console.log(res.data.noticeData)
        console.log('currentPage===', this.pageSize)
          //this.noticeData = res.data.noticeData
          const notiData= res.data.noticeData

          this.total = notiData.length
          let currentMaxLow = this.currentPage * this.pageSize
          let currentMinLow = currentMaxLow - this.pageSize
          this.noticeData = notiData.slice(currentMinLow, currentMaxLow)
          console.log('currentMaxLow = ', currentMaxLow)
      })
      .catch(err => {

      })
      .finally(_ => {

      })
     }
   }
  //  beforeRouteEnter (to, from, next) {
  //     console.log('==== to ====')
  //     console.log(to)
  //     console.log('==== to ====')


  //     console.log('==== from ====')
  //     console.log(from)
  //     console.log('==== from ====')
      
  //     console.log(Cookies.get('token'))

  //     if(Cookies.get('token')){
  //       if(to.path === '/login'){
  //         next({path:'/'})
  //       }else {
  //         next()
  //       }
  //     }else {
  //       if(to.path !== '/login'){
  //         next({path:'/login'})
  //         //next({path:'/login?redirect=${to.path}'})
  //       }else {
  //         next()
  //       }
  //     }
  //    }
  }
</script>



